














































































import Vue from "vue";

export default Vue.extend({
  data: () => ({
    name: "",
    password: "",
    loading: false
  }),
  computed: {
    state() {
      if (this.name.length === 0 || this.password.length === 0)
        return "invalid";
      else if (this.loading) return "loading";
      else return "valid";
    }
  },
  methods: {
    onClick() {
      if (this.state == "valid") {
        this.loading = true;
        // TODO log in
      }
    }
  }
});
